.gallery {
  width: 100%;
  position: absolute;
  top: 4em;
  @media (max-width: 600px) {
    top: 1em;
  }
}
.containers {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 1em;
  grid-auto-rows: minmax(300px, auto);
  margin: 3em auto;
}
.head {
  font-size: 35px;
  color: black;
  padding: 0.5em 0;
  margin: 0 0 0.5em 0;
  border-bottom: 1px solid #ddd;
  text-align: center;
  @media (max-width: 600px) {
    margin: 0.5em;
    font-size: 30px;
  }
}

