.b-1 {
  grid-column: 1/3;
  grid-row: 1/3;
  border-radius: 20px;
  @media (max-width: 600px) {
    grid-row: 1/2;
    grid-column: 1/2;
  }
  div {
    background-image: url("https://www.pagalguy.com/wp-content/uploads/2018/10/E94F6CAC710489E8.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
    padding: 0 !important;
    border: none;
    border-radius: 20px;
  }
}
.box {
  border-radius: 20px;
}
.b-2 {
  div {
    background-image: url("https://images.shiksha.com/mediadata/images/1550666036phpEs1rUP_g.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
    padding: 0 !important;
    border: none;
    border-radius: 20px;
  }
}
@media (max-width: 988px) {
  .box {
    grid-column: 1/12;
  }
  .b-1 {
    grid-column: 1/12;
  }
}
@media (max-width: 420px) {
  .container {
    grid-auto-rows: minmax(250px, auto);
    margin: 0.75em;
  }

  .b-1 {
    grid-column: 1/12;
    grid-row: 1;
  }
  .box {
    grid-column: 1/12;
  }
}

.spin {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 75vh;
  width: 100%;
  img {
    width: 10%;
    @media (max-width: 600px) {
      width: 30%;
    }
  }
}
