.cards{width:90%;border:1px solid #555;padding:.5em}@media (max-width: 600px){.cards{width:100%}}.cards h4{text-align:center;font-size:17px;font-weight:bold}.cards .imgs{margin:0.5em 0}.cards .imgs img{width:100%}.cards h6{font-weight:bold;font-size:15px;color:skyblue}.cards p{text-align:center;color:grey;font-size:14px}.cards .btns{display:flex;justify-content:center}.cards .btns button{background-color:skyblue;color:white;padding:.5em;border-radius:8px;font-size:12px}

