.high {
  margin: 40px 0;
  font-family: "Epilogue", sans-serif;
  font-weight: 600;
  text-align: center;
  text-transform: capitalize;
  @media (max-width: 600px) {
    font-size: 30px;
  }
}
.highlight {
  display: flex;
  width: 80%;
  align-items: center;
  justify-content: space-around;
  margin: 2em auto;
  background-color: white;
  a{
      text-decoration: none !important;
      font-size: 13px;
  }
  @media (max-width: 600px) {
    flex-direction: column;
  }
}
.back {
  background-image: url("https://mycoach.co.za/wp-content/uploads/2018/09/businessman-with-entrepreneurship-hologram-concept-choose-creative-from-words_rxt5xkufe_thumbnail-full12.png");
  height: 22em;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 20px;
  width: 30%;
  text-decoration: none;
  @media (max-width: 600px) {
    width: 100%;
    margin: auto;
    margin-bottom: 1em;
    height: 20em !important;
  }
}
.bg {
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  height: 100%;
  opacity: 0;
  padding: 1em;
  border-radius: 20px;
  cursor: pointer;
  @media (max-width: 600px) {
    padding: 0.3em;
    height: 100%;
  }
  p {
    border: 4px solid white;
    padding: 1em;
    height: 100%;
    border-radius: 20px;
    @media (max-width: 600px) {
      border-width: 2px;
      font-size: 13px;
    }
  }
}
.back:hover .bg {
  opacity: 1;
  transition: 1s ease-in;
}
